import React from "react";

const TodoHeader = () => {
    return (
        <>
            <div className="title headerItem"></div>
            <div className="description headerItem"></div>
            <div className="deadline headerItem"> </div>
            <div className="inner headerItem"> </div>
            <div className="innerButton headerItem"></div>
            <div className="complete headerItem"></div>
            <div className="change headerItem"></div>
            <div className="delete headerItem"></div>
        </>
    );
};

export default TodoHeader;
