/**     ,       validatorConfig        */

const validator = (data, config) => {
    const errors = {};

    function validate(validateMethod, data, config) {
        let statusValidate;

        /**               */
        if (validateMethod === "isRequired") {
            statusValidate = data.trim() === "";
        }

        /**          ...      , ..   ,   01  12,    2000  2099       */
        if (validateMethod === "isDate") {
            const dateRegExp =
                /^([0-9][0-9])[-/.]((0[1-9]|1[012])[-/.](20)[0-9][0-9])$/;
            statusValidate = !dateRegExp.test(data);
        }

        /**           (  ..   )       */
        if (validateMethod === "isCorrectDate") {
            const [day, month, year] = data.split(".")
            switch (month) {
                case "01": case "03": case "05": case "07": case "08": case "10": case "12":
                    if (Number(day) > 31) {
                        statusValidate = true
                    }
                    break;
                case "04": case "06": case "09": case "11":
                    if (Number(day) > 30) {
                        statusValidate = true
                    }
                    break;
                case "02":
                    if (
                        (Number(year) % 4 !== 0 && Number(day) > 28) ||
                        (Number(year) % 4 === 0 && Number(day) > 29)
                    ) {
                        statusValidate = true
                    }
                    break;
                default:
                    statusValidate = false
                    break;
            }
        }

        if (statusValidate) return config.message;
    }

    /**       ,   validatorConfig,     .       ,      ,            */
    for (const fieldName in data) {
        for (const validateMethod in config[fieldName]) {
            const error = validate(
                validateMethod,
                data[fieldName],
                config[fieldName][validateMethod]
            );

            if (error && !errors[fieldName]) {
                errors[fieldName] = error;
            }
        }
    }

    return errors;
};

export default validator;
