import React, { useEffect, useState } from "react";
import TextField from "./textField";
import validator from "../utils/validator"
import transformDate from "../utils/transformDate";
import dayjs from "dayjs";

const ChangeTaskForm = ({ changeTask, todoItem }) => {

    /**              ,      dayjs       */
    const [data, setData] = useState({
        ...todoItem,
        deadline: dayjs(todoItem.deadline).format("DD.MM.YYYY")
    });
    const [errors, setErrors] = useState({});
    const isValid = !Object.keys(errors).length;

    /**                   */
    useEffect(() => {
        validate();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [data]);

    /**             name  value            */
    const handleChange = (target) => {
        setData((prevState) => ({
            ...prevState,
            [target.name]: target.value
        }));
    };

    /**     ,   name    ,           */
    const validatorConfig = {
        title: {
            isRequired: {
                message: "  "
            }
        },
        description: {
            isRequired: {
                message: "  "
            }
        },
        deadline: {
            isRequired: {
                message: "  "
            },
            isDate: {
                message: "      .."
            },
            isCorrectDate: {
                message: "   "
            }
        }
    };

    /**      -     errors    -              */
    const validate = () => {
        const errors = validator(data, validatorConfig);

        setErrors(errors);
        return !Object.keys(errors).length;
    };

    /**       ,    ,    ,  dayJS,             Firebase             */
    const handleSubmit = async (e) => {
        e.preventDefault();
        const isValid = validate();
        if (isValid) {
            data.deadline = transformDate(data.deadline)
            try {
                await changeTask(data);
            } catch (error) {
                console.log(error);
            }
        }
    };

    return (<>
        <form className="form" onSubmit={handleSubmit}>
            <TextField
                label=" "
                name="title"
                value={data.title}
                onChange={handleChange}
                error={errors.title}
            />
            <TextField
                label=" "
                name="description"
                value={data.description}
                onChange={handleChange}
                error={errors.description}
            />
            <TextField
                label=" "
                name="deadline"
                value={data.deadline}
                onChange={handleChange}
                error={errors.deadline}
            />
            <button
                type="submit"
                className="button popapButton"
                disabled={!isValid}
            >
                
            </button>
        </form>
    </>
    );
};

export default ChangeTaskForm;